import React, {useEffect} from 'react';
import {useParams} from 'react-router-dom';
import styles from './Checkout.module.scss';
import Button from '../../../../common/Button/Button';
import useTypedSelector from '../../../../../store/selectors';
import {IStage} from '../Main/Main';
import {generateFields} from './fields';

interface ICheckoutProps {
  click?: () => void,
  activeIndex: number,
  stages: IStage [],
  loading?: boolean,
}

function Checkout(props: ICheckoutProps) {
  const {
    click, activeIndex, stages, loading,
  } = props;
  const {id} = useParams();
  const form = useTypedSelector((state) => state.form);
  let fields = generateFields(form);

  function generatePrice() {
    if (form.tariff && form.dateFrom && form.dateTo) {
      //    ,   
      return ` ${form.priceMin} .`;
    }
    return `  ${form.priceMin}  ${form.priceMax} .`;
  }

  useEffect(() => {
    fields = generateFields(form);
  }, [form]);

  return (
    <div className={styles.checkout}>
      <h3 className={styles.header}> :</h3>
      <ul className={styles.details}>
        {fields.map((field) => {
          //    -  
          return field.value && (
            <li key={field.label} className={styles.row}>
              <span className={styles.label}>{field.label}</span>
              <span />
              <span className={styles.value}>
                {field.label === ' '
                  ? (
                    <>
                      {/*    -    */}
                      <span>
                        {field.value.split(', ')[0]}
                      </span>
                      <br />
                      <span>
                        {field.value.split(', ')[1]}
                      </span>
                    </>
                  )
                  : field.value}
              </span>
            </li>
          );
        })}
      </ul>
      {form.priceMin && (
        <p className={styles.price}>
          :
          <span>
            {generatePrice()}
          </span>
        </p>
      )}
      <Button
        click={click}
        className={styles.button}
        color={stages[activeIndex].vars.includes('') ? 'blocked' : id ? 'magenta' : ''}
        disabled={stages[activeIndex].vars.includes('')}
        loading={loading}
      >
        {id ? '' : stages[activeIndex].buttonLabel}
      </Button>
    </div>
  );
}

export default Checkout;
